@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300&family=Poppins:wght@300;600&display=swap);
.page-label {
  -webkit-transform: translate(-48vw, -20vh) rotate(-90deg);
          transform: translate(-48vw, -20vh) rotate(-90deg);
}

.button {
  --dimension: 20px;
  height: var(--dimension);
  width: var(--dimension);
  margin-bottom: 20px;
  border-radius: var(--dimension);
  background-color: var(--Black50);
  transition: 0.2s;
}

.button:hover {
  --dimension-hover: 25px;
  background-color: white;
}


.title {
  text-align: center;
}


.parent-container {
  display: flex;
  flex-direction: column;
}

.section-container {
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
  overflow-x: scroll;
  overflow: scroll;
  display: flex;
  height: 92vh;
  width: 100vw;
}

.bottom-buttons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: sticky;
  position: -webkit-sticky;
  height: 8vh;
  width: 100vw;
}

section {
  scroll-snap-align: start;
  position: relative;
  height: 100vh;
  min-width: 100vw;
}

/* --- Globals --- */

:root {
  --Primary: #a11d6a;
  --Secondary: #951fff;
  --Linear: linear-gradient(to right, var(--Primary), var(var(--Secondary)));
  --Error: #e00248;

  --Black: #262626;
  --Black75: #666666;
  --Black50: #a6a6a6;
  --Black25: #e6e6e6;
  --Black10: #f2f2f2;
}

.Monospace {
  font-size: 0.875rem;
  font-weight: 300;
  font-family: "JetBrains Mono", monospace;
  color: #e6e6e6;
  color: var(--Black25);
}

.Paragraph {
  font-size: 0.875rem;
  font-weight: 300;
  font-family: "Poppins", sans-serif;
  color: #e6e6e6;
  color: var(--Black25);
}

h1 {
  font-size: 7.063rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

h2 {
  font-size: 5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

h3 {
  font-size: 3.563rem;
  font-weight: 300;
  font-family: "Poppins", sans-serif;
  color: white;
}

h4 {
  font-size: 2.5rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

h5 {
  font-size: 1.75rem;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  color: white;
}

html, body {
  background: #262626;
  background: var(--Black);
  scrollbar-width: none;
  scroll-behavior: smooth;
  height: 100vh;
}

* {
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
  display: none;
}

